import { Preloader, ErrorMessage, Breadcrumb } from "../../components";
import useFetch from "../../hooks/use-fetch";
import { useSelector } from "react-redux";
import { userService } from "../../services/user.service";
import { DUMMY_USER_URL } from "../../config/config";

const Order = () => {
  const { currentUser } = useSelector(state => state.user)
  const { data, isLoading, errorMessage } = useFetch("order.json");

  if (isLoading) return <Preloader />;
  if (errorMessage) return <ErrorMessage errorMessage={errorMessage} />

  return (
    <div className="body-wrapper space-pb--120">
      <Breadcrumb pageTitle="Orders" />
      <div className="order-product-area">
        {userService.getContactPurchaseType(currentUser).map((transaction) => (
          <div
            className="cart-product border-bottom--medium"
            key={transaction._id}
          >
            <div className="cart-product__image">
              <img
                src={transaction?.contact?.img?.url || DUMMY_USER_URL}
                className="img-fluid"
                alt=""
              />
            </div>
            <div className="cart-product__content">
              <h3 className="title">{transaction.contact.desc}</h3>
              <span className="category">
                {transaction.contact.category}
              </span>
              <div className="price">
                <span className="discounted-price">{`$${transaction.contact.price}`}</span>
              </div>
            </div>
            {/* <div className="cart-product__status">
              <p>
                <span>
                  {single.orderStatus === "completed" ? (
                    <FaRegCheckCircle />
                  ) : single.orderStatus === "cancelled" ? (
                    <FaRegTimesCircle />
                  ) : (
                    <FaRedo />
                  )}
                </span>{" "}
                {single.orderStatus}
              </p>
            </div> */}
          </div>
        ))}
      </div>
    </div>
  );
}

export default Order;
